            console.log('获取"' + this.data.fields[i].label + '"参数结果=>' + JSON.stringify(this.$appdata.getParam(this.data.fields[i].label)))
            if(this.$appdata.getParam(this.data.fields[i].label)){
              console.log('不是undefined，该下拉框使用数据库参数')
              this.data.fields[i].options = this.$appdata.getParam(this.data.fields[i].label)
            }else{
              console.log('是undefined，该下拉框使用后台function_vue.json该field的默认下拉配置')
            }
          }
          if(typeof this.data.fields[i].value ==='string' && this.data.fields[i].value.startsWith("{")&&this.data.fields[i].value.endsWith("}")){
            this.data.fields[i].value = JSON.parse(this.data.fields[i].value)
          }

