           if (this.$refs.serviceview.data.fields[i].label === '街道' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address += this.$refs.serviceview.data.fields[i].value
           }else if (this.$refs.serviceview.data.fields[i].label === '街道' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address += this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '小区' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address +=  this.$refs.serviceview.data.fields[i].value
           }else if (this.$refs.serviceview.data.fields[i].label === '小区' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address +=  this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '单位名称' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address +=  this.$refs.serviceview.data.fields[i].value
           }else if (this.$refs.serviceview.data.fields[i].label === '单位名称' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address +=  this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '楼号' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address +=  this.$refs.serviceview.data.fields[i].value+ '栋'
           }else if (this.$refs.serviceview.data.fields[i].label === '楼号' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address += this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '单元号' && this.$refs.serviceview.data.fields[i].value  &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address +=this.$refs.serviceview.data.fields[i].value+ '单元'
           }else if (this.$refs.serviceview.data.fields[i].label === '单元号' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address +=this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '楼层' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address += this.$refs.serviceview.data.fields[i].value+ '层'
           }else if (this.$refs.serviceview.data.fields[i].label === '楼层' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address += this.$refs.serviceview.data.fields[i].value.name
           }
           if (this.$refs.serviceview.data.fields[i].label === '门牌号' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'string') {
             address +=  this.$refs.serviceview.data.fields[i].value+ '室'
           }else if (this.$refs.serviceview.data.fields[i].label === '门牌号' && this.$refs.serviceview.data.fields[i].value &&  typeof this.$refs.serviceview.data.fields[i].value === 'object'){
             address += this.$refs.serviceview.data.fields[i].value.name
           }
         }
         console.log('获取到的address=>' + address)
         // 数据获取完毕时放入地址text
         for (let i = 0; i < this.$refs.serviceview.data.fields.length; i++) {
           if (this.$refs.serviceview.data.fields[i].label === '地址') {
             this.$refs.serviceview.data.fields[i].value = address
             console.log('修改fields[i].value后的值=>' + this.$refs.serviceview.data.fields[i].value)
           }
         }
       }
       this.$refs.serviceview.update()
      },
      // 获取view层button事件
      async 'button'(model){
        model=Object.assign({},this.selectdata,model)
        console.log("button事件接收参数：",model)
        // console.log("this.data=>" + JSON.stringify(this.data))
        let type = '通用业务'
        if(this.data.title == '信息变更'){
          type = '信息变更'
        }
        // 下发，提交，保存，退回都在后台处理
        let http = new HttpResetClass();
        let res = await http.load('POST','rs/logic/ApplyProductFunctionService',{data:{tables:this.funcdata.tables,model:model,loginUser:this.$login.f, type: type}}, {resolveMsg: null, rejectMsg: '数据保存失败'})
        console.log("service处理完成返回数据：",JSON.stringify(res.data))
        if(res.code=200){
          this.$dispatch('hiddenbtn')
          this.showview= false
        }else{
          throw res.data.msg
        }
      }
    },
    // watch:{
    //   'selectdata.f_process_id'(){
    //     this.selectdata = this.selectdata
    //   },
    //   deep:true
    // }
  }
</script>



